//  echarts  echarts 
import * as echarts from "echarts/core";

/** and Chart  */
import { BarChart, LineChart } from "echarts/charts";

//  Component
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  VisualMapComponent,
  LegendComponent,
} from "echarts/components";

// 
import { LabelLayout, UniversalTransition } from "echarts/features";

//  Canvas  CanvasRenderer  SVGRenderer 
import { CanvasRenderer } from "echarts/renderers";

import { SurfaceChart } from "echarts-gl/charts";
import { Grid3DComponent } from "echarts-gl/components";

// 
echarts.use([
  TitleComponent,
  LegendComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  BarChart,
  LabelLayout,
  UniversalTransition,
  CanvasRenderer,
  LineChart,
  VisualMapComponent,
  SurfaceChart,
  Grid3DComponent,
]);

// 
export default echarts;
