const formatSize = (size) => {
  let data = "";
  if (size < 0.1 * 1024) {
    //0.1KBB
    data = size.toFixed(2) + "B";
  } else if (size < 0.1 * 1024 * 1024) {
    //0.1MBKB
    data = (size / 1024).toFixed(2) + "KB";
  } else if (size < 0.1 * 1024 * 1024 * 1024) {
    //0.1GBMB
    data = (size / (1024 * 1024)).toFixed(2) + "MB";
  } else {
    //GB
    data = (size / (1024 * 1024 * 1024)).toFixed(2) + "GB";
  }
  let sizestr = data + "";
  let len = sizestr.indexOf(".");
  let dec = sizestr.substr(len + 1, 2);
  if (dec == "00") {
    return sizestr.substring(0, len) + sizestr.substr(len + 3, 2);
  }
  return sizestr;
};

const formatTime = (mss) => {
  const hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
  const minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));

  return hours + ":" + minutes;
};

const getNowFormatDate = (min) => {
  let date = new Date();
  let seperator2 = ":";
  //

  let interTimes = min * 60 * 1000;
  date = new Date(Date.parse(date) - +interTimes);

  let month = date.getMonth() + 1;
  let strDate = date.getDate();
  let hour = date.getHours();
  let minutes = date.getMinutes();
  let seconds = date.getSeconds();

  if (month >= 1 && month <= 9) {
    month = "0" + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = "0" + strDate;
  }
  if (hour >= 0 && hour <= 9) {
    hour = "0" + hour;
  }
  if (minutes >= 0 && minutes <= 9) {
    minutes = "0" + minutes;
  }
  if (seconds >= 0 && seconds <= 9) {
    seconds = "0" + seconds;
  }
  let currentdate = hour + seperator2 + minutes + seperator2 + seconds;
  return currentdate;
};

//
const formatDateTime = function (time, format) {
  const t = new Date(time * 1000);
  const tf = (i) => (i < 10 ? "0" : "") + i;
  return format.replace(/yyyy|MM|dd|HH|mm|ss/g, function (a) {
    switch (a) {
      case "yyyy":
        return tf(t.getFullYear());
      case "MM":
        return tf(t.getMonth() + 1);
      case "mm":
        return tf(t.getMinutes());
      case "dd":
        return tf(t.getDate());
      case "HH":
        return tf(t.getHours());
      case "ss":
        return tf(t.getSeconds());
    }
  });
};

