// 
export function timestampToTime(timestamp) {
  var date = new Date(timestamp * 1000); //10*1000131000
  var Y = date.getFullYear() + "-";
  var M =
    (date.getMonth() + 1 < 10
      ? "0" + (date.getMonth() + 1)
      : date.getMonth() + 1) + "-";
  var D = date.getDate() + " ";
  var h = date.getHours() + ":";
  var m = date.getMinutes() + ":";
  var s = date.getSeconds();
  return Y + M + D + h + m + s;
}
// 
const loadFile = function (name) {
  // name
  let xhr = new XMLHttpRequest();
  const okStatus = document.location.protocol === "file:" ? 0 : 200;
  xhr.open("GET", name, false);
  xhr.overrideMimeType("text/html;charset=utf-8"); //utf-8
  xhr.send(null);
  return xhr.status === okStatus ? xhr.responseText : null;
};
// 1.  timer=timeout
const throttle = function (fn, time) {
  //3.  "" false
  let temp = false;
  return function () {
    //8. "" true  truetrurn
    if (temp) {
      return;
    } else {
      //4. false  true
      temp = true; //true
      //5.  
      setTimeout(() => {
        //6. setTimeout
        fn.apply(this, arguments);
        //7. setTimeout''false()  truereturn
        temp = false;
      }, time);
    }
  };
};
// ()
const debounceFn = (fun, delay = 500) => {
  var timer;
  return function () {
    if (timer) {
      clearTimeout(timer);
      timer = null;
    }
    timer = setTimeout(() => {
      fun.call(this, ...arguments);
    }, delay);
  };
};
const downFile = (url, name) => {
  console.log(url, "url");
  let x = new XMLHttpRequest();
  x.open("GET", url, true);
  x.responseType = "blob";
  x.onload = (e) => {
    let url = window.URL.createObjectURL(x.response);
    let a = document.createElement("a");
    a.href = url;
    a.download = name;
    a.click();
  };
  x.send();
};
// dom
const addClass = function(ele, str) {
  if (ele.className) {
    return
  } else {
    ele.className = str;
  }
}
