import { messageCenter } from "./messageCenter.js";
const ModeCode = {
  //websocket
  MSG: "message", //
  HEART_BEAT: "heart_beat", //
};
// // 
// const env = process.env.NODE_ENV;
// // IP
// const proIp = window.location.hostname;
// // WebSocket
// // 172
// const wsUrlLocal = "ws://172.21.10.97:9999";
// // 127
// const wsUrlProd = "ws://" + proIp + ":9999";
// console.log("websocketIP", wsUrlProd);
// const wsUrl = env === "production" ? wsUrlProd : wsUrlLocal;
const heartBeatConfig = {
  time: 5 * 1000, // 
  timeout: 1 * 1000, // 
  reconnect: 5 * 1000, // 
};
const isReconnect = true; // 
export class MyWebSocket extends WebSocket {
  constructor(url) {
    // Thisprototype,super.fun.call(this)This,
    super(url);
    return this;
  }

  /*
   * 
   * @param heartBeatConfig  time timeout reconnect
   * @param isReconnect 
   */
  init() {
    // WebSocket ,,,
    this.onopen = this.openHandler; // 
    this.onclose = this.closeHandler; // 
    this.onmessage = this.getMsg; // 
    this.onerror = this.errorHandler; //
    this.heartBeat = heartBeatConfig;
    this.isReconnect = isReconnect;
    this.reconnectTimer = null; //
    this.waitingTimer = null; // 
    this.heartTimer = null; // 
    this.webSocketState = false; //socket true
  }
  // websocket,
  openHandler() {
    this.webSocketState = true; //socket
    !!this.heartBeat &&
      !!this.heartBeat.time &&
      this.startHeartBeat(this.heartBeat.time); // 
    // console.log("websocket,", this.readyState);
    messageCenter.emit("websocket_open", this.readyState);
  }
  // ,websocket
  getMsg(e) {
    let data = e.data;
    this.webSocketState = true;
    // console.log("websocket:" + data);
    messageCenter.emit("message_change", data);

    // switch (data.ModeCode) {
    //   case ModeCode.MSG: // 
    //     console.log("" + data.msg);
    //     break;
    //   case ModeCode.HEART_BEAT: // 
    //     console.log("" + data.msg);
    //     break;
    // }
  }
  // websocket
  closeHandler() {
    //socket
    //   messageCenter.emit("changeBtnState", "close"); //
    this.webSocketState = false; //socket
    // console.log("socket");
  }
  // websocket: ,,
  errorHandler() {
    //socket
    this.webSocketState = false; //socket
    this.reconnectWebSocket(); //
    // console.log(
    //   "websocket: ,,"
    // );
  }
  // websocket 
  sendMsg(obj) {
    // console.log("socket: ", JSON.stringify(obj));
    try {
      this.send(JSON.stringify(obj));
    } catch (error) {
      messageCenter.emit("socket_close", "Failed");
    }
  }

  /*
   * 
   * @param time
   */
  startHeartBeat(time) {
    this.heartTimer = setTimeout(() => {
      this.sendMsg({
        ModeCode: ModeCode.HEART_BEAT,
        msg: new Date(),
      });
      this.waitingTimer = this.waitingServer();
      // console.log(": ", this.waitingTimer);
    }, time);
  }

  //webSocketState
  waitingServer() {
    this.webSocketState = false;
    return setTimeout(() => {
      if (this.webSocketState) return this.startHeartBeat(this.heartBeat.time);
      // console.log("");
      this.reconnectTimer = this.reconnectWebSocket();
    }, this.heartBeat.timeout);
  }

  //
  //
  reconnectWebSocket() {
    if (!this.isReconnect) return;
    return setTimeout(() => {
      messageCenter.emit("reconnect");
    }, this.heartBeat.reconnect);
  }
  // 
  clearTimer() {
    clearTimeout(this.reconnectTimer);
    clearTimeout(this.heartTimer);
    clearTimeout(this.waitingTimer);
  }
  // 
  clear(isReconnect = false) {
    this.isReconnect = isReconnect;
    this.clearTimer();
    this.close();
    // console.log("websocket");
  }
}
